import { useState, useEffect, useCallback } from "react";
import {
  createPaymentTransaction,
  createApplCreateTransaction,
  createApplUpdateTransaction,
  createApplOptInTransaction,
  createApplOptOutTransaction,
  createApplDeleteTransaction,
  waitForConfirmation,
} from "./AlgorandUtils";
import "./styles.css";

type ExodusEvent = "connect" | "disconnect";
type ExodusRequestMethod =
  | "connect"
  | "disconnect"
  | "signTransaction"
  | "signAndSendTransaction";

interface ExodusProvider {
  address: string | null;
  isConnected: boolean | null;
  signTransaction: (transaction: any[]) => Promise<Uint8Array[]>;
  signAndSendTransaction: (transactions: any[]) => Promise<{ txId: string }>;
  connect: () => Promise<{ address: string }>;
  disconnect: () => Promise<void>;
  on: (event: ExodusEvent, handler: (args: any) => void) => void;
  request: (method: ExodusRequestMethod, params: any) => Promise<unknown>;
}

const getProvider = (): ExodusProvider | undefined => {
  if ("exodus" in window) {
    const anyWindow: any = window;
    return anyWindow.exodus.algorand;
  }

  window.open("https://exodus.com", "_blank");
};

export default function App() {
  const provider = getProvider();
  const [logs, setLogs] = useState<string[]>([]);
  const addLog = useCallback((log: string) => {
    console.log(log);
    setLogs((logs) => [...logs, "> " + log]);
  }, []);
  const [, setConnected] = useState<boolean>(false);
  const [address, setAddress] = useState<string | null>(null);
  const [createdAppId, setCreatedAppId] = useState<number | null>(null);
  const [optedInAppId, setOptedInAppId] = useState<number | null>(null);
  useEffect(() => {
    if (!provider) return;
    // try to eagerly connect
    provider.connect().catch((err) => {
      // fail silently
    }).then((a: any) => {
      setAddress(a.address);
      setConnected(true);
      addLog("[connect] " + address);
    });
    // provider.on("connect", (address: string) => {
    //   setAddress(address);
    //   setConnected(true);
    //   addLog("[connect] " + address);
    // });
    // provider.on("disconnect", () => {
    //   setAddress(null);
    //   setConnected(false);
    //   addLog("[disconnect] ðŸ‘‹");
    // });
    return () => {
      provider.disconnect();
    };
  }, [provider, addLog]);
  if (!provider) {
    return <h2>Could not find a provider</h2>;
  }

  const signTransaction = async () => {
    let signedTransactions = [];
    try {
      const [transaction1] = await Promise.all([
        createPaymentTransaction(provider.address),
      ]);
      if (transaction1) {
        signedTransactions = await provider.signTransaction([transaction1]);
        addLog(
          "signTransaction signedTransactions: " +
            JSON.stringify(signedTransactions)
        );
      }
    } catch (err) {
      console.warn(err);
      addLog("[error] signTransactions: " + JSON.stringify(err));
    }
    return signedTransactions;
  };

  const signAndSendTransaction = async () => {
    try {
      const transactions = await Promise.all([
        createPaymentTransaction(provider.address),
      ]);
      const { txId } = await provider.signAndSendTransaction(transactions);
      addLog("Sending transaction " + txId + ", awaiting confirmation");
      await waitForConfirmation(txId, 10);
      addLog("Transaction " + txId + " confirmed");
    } catch (err) {
      console.warn(err);
      addLog("[error] signAndSendTransaction: " + JSON.stringify(err));
    }
  };

  const signAndSendApplCreateTransaction = async () => {
    let signedTransactions = [];
    try {
      const transactions = await Promise.all([
        createApplCreateTransaction(provider.address),
      ]);
      if (transactions) {
        const { txId } = await provider.signAndSendTransaction(transactions);
        addLog("Sending transaction " + txId + ", awaiting confirmation");
        const confirmedTx = await waitForConfirmation(txId, 10);
        addLog("Transaction " + txId + " confirmed");
        const appId = confirmedTx["application-index"];
        addLog("Created application with appId " + appId);
        setCreatedAppId(appId);
      }
    } catch (err) {
      console.warn(err);
      addLog(
        "[error] signAndSendApplCreateTransaction: " +
          err.toString() +
          " " +
          JSON.stringify(err)
      );
    }
    return signedTransactions;
  };

  const signAndSendApplUpdateTransaction = async () => {
    let signedTransactions = [];
    try {
      const transactions = await Promise.all([
        createApplUpdateTransaction(provider.address, createdAppId),
      ]);
      if (transactions) {
        const { txId } = await provider.signAndSendTransaction(transactions);
        addLog("Sending transaction " + txId + ", awaiting confirmation");
        await waitForConfirmation(txId, 10);
        addLog("Transaction " + txId + " confirmed");
        addLog("Updated application with appId " + createdAppId);
      }
    } catch (err) {
      console.warn(err);
      addLog(
        "[error] signAndSendApplUpdateTransaction: " +
          err.toString() +
          " " +
          JSON.stringify(err)
      );
    }
    return signedTransactions;
  };

  const signAndSendApplOptInTransaction = async () => {
    let signedTransactions = [];
    try {
      const transactions = await Promise.all([
        createApplOptInTransaction(provider.address, createdAppId),
      ]);
      if (transactions) {
        const { txId } = await provider.signAndSendTransaction(transactions);
        addLog("Sending transaction " + txId + ", awaiting confirmation");
        await waitForConfirmation(txId, 10);
        addLog("Transaction " + txId + " confirmed");
        addLog("Opted In application with appId " + createdAppId);
        setOptedInAppId(createdAppId);
      }
    } catch (err) {
      console.warn(err);
      addLog(
        "[error] signAndSendApplOptinTransaction: " +
          err.toString() +
          " " +
          JSON.stringify(err)
      );
    }
    return signedTransactions;
  };

  const signAndSendApplOptOutTransaction = async () => {
    let signedTransactions = [];
    try {
      const transactions = await Promise.all([
        createApplOptOutTransaction(provider.address, optedInAppId),
      ]);
      if (transactions) {
        const { txId } = await provider.signAndSendTransaction(transactions);
        addLog("Sending transaction " + txId + ", awaiting confirmation");
        await waitForConfirmation(txId, 10);
        addLog("Transaction " + txId + " confirmed");
        addLog("Opted Out application with appId " + optedInAppId);
        setOptedInAppId(null);
      }
    } catch (err) {
      console.warn(err);
      addLog(
        "[error] signAndSendApplOptoutTransaction: " +
          err.toString() +
          " " +
          JSON.stringify(err)
      );
    }
    return signedTransactions;
  };

  const signAndSendApplDeleteTransaction = async () => {
    let signedTransactions = [];
    try {
      const transactions = await Promise.all([
        createApplDeleteTransaction(provider.address, createdAppId),
      ]);
      if (transactions) {
        const { txId } = await provider.signAndSendTransaction(transactions);
        addLog("Sending transaction " + txId + ", awaiting confirmation");
        await waitForConfirmation(txId, 10);
        addLog("Transaction " + txId + " confirmed");
        addLog("Deleted application with appId " + createdAppId);
        setCreatedAppId(null);
        setOptedInAppId(null);
      }
    } catch (err) {
      console.warn(err);
      addLog(
        "[error] signAndSendApplDeleteTransaction: " +
          err.toString() +
          " " +
          JSON.stringify(err)
      );
    }
    return signedTransactions;
  };

  return (
    <div className="App">
      <main>
        <h1>Exodus Sandbox</h1>
        {provider && address ? (
          <>
            <div>
              <pre>Connected as</pre>
              <br />
              <pre>{address}</pre>
              <br />
            </div>
            <button onClick={signTransaction}>Sign Transaction</button>
            <button onClick={signAndSendTransaction}>
              Sign And Send Transaction
            </button>
            <button onClick={signAndSendApplCreateTransaction}>
              Sign And Send Application Create Transaction
            </button>
            <button onClick={signAndSendApplOptInTransaction}>
              Sign And Send Application Opt In Transaction
            </button>
            <button onClick={signAndSendApplOptOutTransaction}>
              Sign And Send Application Opt Out Transaction
            </button>
            <button onClick={signAndSendApplUpdateTransaction}>
              Sign And Send Application Update Transaction
            </button>
            <button onClick={signAndSendApplDeleteTransaction}>
              Sign And Send Application Delete Transaction
            </button>
            <button
              onClick={async () => {
                try {
                  await provider.disconnect();
                } catch (err) {
                  console.warn(err);
                  addLog("[error] disconnect: " + JSON.stringify(err));
                }
              }}
            >
              Disconnect
            </button>
          </>
        ) : (
          <>
            <button
              onClick={async () => {
                try {
                  await provider.connect();
                } catch (err) {
                  console.warn(err);
                  addLog("[error] connect: " + JSON.stringify(err));
                }
              }}
            >
              Connect to Exodus
            </button>
          </>
        )}
      </main>
      <footer className="logs">
        {logs.map((log, i) => (
          <div className="log" key={i}>
            {log}
          </div>
        ))}
      </footer>
    </div>
  );
}
